import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import JobsViewTopNavConnector from 'Candidate/connectors/JobsViewTopNav/JobsViewTopNavConnector'
import withTranslations from 'Shared/hoc/withTranslations/index'
import { JobAlertsListMobile } from './components/JobAlertsList'
import { JobAlertsEmptyStateMobile } from './components/JobAlertsEmptyState'
import { isNotEmpty } from 'Shared/helpers/Ramda'
import { FormModalMobile } from 'Shared/components/Modal'
import CreateJobAlertFormConnector from 'Candidate/connectors/Forms/JobAlerts/CreateJobAlertFormConnector'

import './JobAlerts.scss'
import { Roles } from 'Shared/constants/User'
import JobOfferListSignUp from 'Candidate/views/JobsView/components/JobOfferListSignUp/JobOfferListSignUp'

const R = require('ramda')

class JobAlertsMobile extends PureComponent {
  state = {
    isModalOpen: false
  }

  componentDidMount () {
    (this.props.userRole === Roles.candidate.name) && this.props.fetchJobAlerts(this.props.candidateId)
  }

  openModal = () => this.setState({ isModalOpen: true })
  closeModal = () => this.setState({ isModalOpen: false })

  get jobAlerts () {
    return R.ifElse(
      R.isEmpty,
      R.always(<JobAlertsEmptyStateMobile submitForm={this.props.submitForm} isFormValid={this.props.isFormValid} />),
      R.always(<JobAlertsListMobile jobAlerts={this.props.jobAlerts} />)
    )(this.props.jobAlerts)
  }

  render () {
    const { t, submitForm, isFormValid, userRole } = this.props

    return (
      <div>
        <JobsViewTopNavConnector />
        <div>
          {
            userRole === Roles.guest.name
              ? <JobOfferListSignUp
                title={t('candidate.jobAlerts.guestTitle')}
                description={t('candidate.jobAlerts.guestDescription')}
              />
              : <div>
                <div className='job-alerts-mobile-list__header'>
                  {t('candidate.jobAlerts.description')}
                </div>
                {this.jobAlerts}
              </div>
          }
        </div>
        {
          isNotEmpty(this.props.jobAlerts) && (
            <div>
              <div onClick={this.openModal} className='job-alerts-mobile__new-btn'>
                <i className='ion-md-add' />
              </div>
              <FormModalMobile
                title={t('common.createJobAlerts')}
                show={this.state.isModalOpen}
                handleClose={this.closeModal}
                handleSubmit={submitForm}
                submitText={t('common.createJobAlert')}
                cancelText={t('common.cancel')}
                disableSubmit={!isFormValid}
              >
                <p className='job-alerts-mobile__form-description'>{t('modals.createJobAlert.description')}</p>
                <CreateJobAlertFormConnector />
              </FormModalMobile>
            </div>
          )
        }
      </div>
    )
  }
}

JobAlertsMobile.propTypes = {
  fetchJobAlerts: PropTypes.func.isRequired,
  jobAlerts: PropTypes.array.isRequired,
  candidateId: PropTypes.string.isRequired
}

export default withTranslations(JobAlertsMobile)



