import { connect } from 'react-redux'
import { submit, isValid } from 'redux-form'
import { fetchJobAlerts } from 'Shared/ducks/jobAlerts'
import { JobAlerts, JobAlertsMobile } from 'Candidate/views/JobAlerts'
import { getJobAlertsAsArray } from 'Shared/selectors/jobAlerts'
import { isMobile } from 'Shared/helpers/mobile'
import { formName } from 'Candidate/connectors/Forms/JobAlerts/CreateJobAlertFormConnector'
import { getUserRole } from 'Shared/selectors/user'

const JobAlertsComponent = isMobile() ? JobAlertsMobile : JobAlerts

const mapStateToProps = state => ({
  jobAlerts: getJobAlertsAsArray(state),
  userRole: getUserRole(state),
  candidateId: state.user.details.id,
  isFormValid: isValid(formName)(state)
})

const mapDispatchToProps = dispatch => ({
  fetchJobAlerts: (candidateId) => dispatch(fetchJobAlerts(candidateId)),
  submitForm: () => dispatch(submit(formName))
})

export default connect(mapStateToProps, mapDispatchToProps)(JobAlertsComponent)



