import React, { Component } from 'react'
import Container from 'Shared/components/Container'
import { t } from 'i18next'
import signUpImage from 'Shared/assets/images/illustrations/employer-empty-state.svg'
import { Roles } from 'Shared/constants/User'
import GetStartedFormConnector from 'Shared/connectors/Forms/GetStartedFormConnector'
import { StaticRoutes } from 'Shared/constants/Routes'
import { Link } from 'react-router-dom'
import './JobOfferListSignUp.scss'

class JobOfferListSignUp extends Component {
  render () {
    const { title, description } = this.props
    return (
      <Container>
        <div className='columns job-offer-list-sign-up__container'>
          <div className='col-6 col-xs-12 col-sm-12 job-offer-list-sign-up__cta'>
            <h3 className='job-offer-list-sign-up__header'>{title}</h3>
            <p className='job-offer-list-sign-up__description'>{description}</p>
            <GetStartedFormConnector
              landing
              btnClassName='button--primary'
              role={Roles.candidate.name}
            />
            <p className='job-offer-list-sign-up__already-registered'>{t('authorization.alreadyHaveAccount')} <Link to={StaticRoutes.candidate.login}>{t('authorization.login')}</Link></p>
          </div>
          <div className='col-6 col-xs-12 col-sm-12 job-offer-list-sign-up__image'>
            <img src={signUpImage} />
          </div>
        </div>
      </Container>
    )
  }
}

export default JobOfferListSignUp



