import { reduxForm } from 'redux-form'
import notie from 'notie'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import JobAlertsForm from 'Candidate/forms/JobAlerts/JobAlertsForm'
import { transformToArray } from 'Shared/helpers/redux'
import FormUtilsService from 'Shared/services/FormUtilsService'
import { EMPLOYMENT_TYPES } from 'Shared/constants/JobPreferences'
import { fetchDepartments } from 'Shared/ducks/departments'
import { fetchCities } from 'Shared/ducks/resources'
import { fetchIndustries } from 'Shared/ducks/industries'
import { getAllCities } from 'Shared/selectors/cities'
import { availabilityCommitmentOptions, jobAlertsFrequencyOptions } from 'Shared/constants/Job'
import { updateJobAlert } from 'Shared/ducks/jobAlerts'
import validate from './validate'
import { t } from 'i18next'

const R = require('ramda')

const formUtilsService = new FormUtilsService()

export const formName = 'EditJobAlertsForm'

const mapOptionsByLanguage = formUtilsService.mapOptionsByLanguageTranslations

const transformJobAlertValues = values => {
  return {
    query: R.dissoc('emailFrequency', values),
    frequency: values.emailFrequency
  }
}

let Form = reduxForm({
  form: formName,
  onSubmit: async (values, dispatch, props) => {
    try {
      const payload = transformJobAlertValues(values)
      await dispatch(updateJobAlert({id: props.jobAlert.id, ...payload}))

      notie.alert({
        type: 'success',
        text: t('alerts.editJobAlertSuccess')
      })
      return props.history.push('/job-alerts')
    } catch (e) {
      return notie.alert({
        type: 'error',
        text: t('alerts.editJobAlertError')
      })
    }
  },
  enableReinitialize: true,
  validate
})(JobAlertsForm)

const mapStateToProps = (state, ownProps) => {
  return ({
    initialValues: {
      ...R.pick(['searchKeyword', 'jobType', 'department', 'commitment', 'industry', 'city'])(ownProps.jobAlert.query),
      emailFrequency: ownProps.jobAlert.frequency
    },
    jobTypesOptions: mapOptionsByLanguage(EMPLOYMENT_TYPES),
    industriesOptions: mapOptionsByLanguage(transformToArray(state.industries.entries)),
    departmentsOptions: mapOptionsByLanguage(transformToArray(state.departments.entries), 'description'),
    commitmentTypes: availabilityCommitmentOptions(),
    emailFrequencyTypes: jobAlertsFrequencyOptions(),
    citiesOptions: getAllCities()(state)
  })
}

const mapDispatchToProps = {
  fetchIndustries,
  fetchDepartments,
  fetchCities
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Form))



