import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { t } from 'i18next'
import notie from 'notie'
import withTranslations from 'Shared/hoc/withTranslations/index'
import { getLabels } from 'Shared/helpers/collection'
import { StaticTag } from 'Shared/components/StaticTag/index'
import { Popup } from 'Shared/components/Popup/index'
import { Select } from 'Shared/components/FormElements/index'
import { jobAlertsFrequencyOptions } from 'Shared/constants/Job'
import { SmallJustIconButton } from 'Shared/components/Button/index'
import Modal from 'Shared/components/Modal/index'
import DeleteModal from 'Shared/components/DeleteModal/DeleteModal'
import EditJobAlertFormConnector from 'Candidate/connectors/Forms/JobAlerts/EditJobAlertFormConnector'
import SeeAllValuesButton from 'Shared/components/SeeAllValuesButton'

const R = require('ramda')

const MAX_LABELS_COUNT = 2

class JobAlertItem extends Component {
  state = {
    isJobAlertFormOpen: false,
    isDeleteJobAlertModalOpen: false
  }

  get labelsForPreferences () {
    const filterPreferences = R.pipe(
      R.omit(['searchKeyword', 'emailFrequency']),
      getLabels
    )(this.props.jobAlert.query)

    return [...filterPreferences, this.props.jobAlert.query.searchKeyword]
  }

  get preferencesTags () {
    const labels = this.labelsForPreferences
    const items = labels.slice(0, MAX_LABELS_COUNT).map((label, index) => <StaticTag
      key={`${index}-${label}`}
      className='job-alert-item__tag'
      name={label}
    />)

    labels.length > MAX_LABELS_COUNT && items.push(
      <Popup
        key={`${labels.length}-last`}
        triggerButton={
          <SeeAllValuesButton
            className='job-alert-item__tag job-alert-item__tag--view'
            value={this.labelsForPreferences.length - MAX_LABELS_COUNT}
          />}
        position='bottom'
      >
        { labels.map((label, index) =>
          <StaticTag
            key={`${index}-${label}`}
            className='job-alert-item__tag'
            name={label}
          />)
        }
      </Popup>
    )

    return items
  }

  get mockedReduxFormProps () {
    return {
      input: {
        value: this.props.jobAlert.frequency
      },
      meta: {}
    }
  }

  onSelectBlur = () => {}

  onUpdateJobAlert = async value => {
    try {
      await this.props.updateJobAlert({ id: this.props.jobAlert.id, frequency: value })
      notie.alert({
        type: 'success',
        text: t('alerts.editJobAlertSuccess')
      })
    } catch (e) {
      notie.alert({
        type: 'error',
        text: t('alerts.editJobAlertError')
      })
    }
  }

  openEditJobAlertForm = () => this.setState({ isJobAlertFormOpen: true })
  closeEditJobAlertForm = () => this.setState({ isJobAlertFormOpen: false })

  openDeleteModal = () => this.setState({ isDeleteJobAlertModalOpen: true })
  hideDeleteModal = () => this.setState({ isDeleteJobAlertModalOpen: false })

  submitForm = () => {
    this.props.submitForm()
    this.props.canCloseModal && this.closeEditJobAlertForm()
  }

  deleteJobAlert = async () => {
    try {
      await this.props.deleteJobAlert(this.props.jobAlert.id)
      this.hideDeleteModal()
      notie.alert({
        type: 'success',
        text: t('alerts.deleteJobAlertSuccess')
      })
    } catch (e) {
      notie.alert({
        type: 'error',
        text: t('alerts.deleteJobAlertError')
      })
    }
  }

  render () {
    const { jobAlert } = this.props

    return (
      <div className='job-alert-item'>
        <div className='columns'>
          <div className='column col-2'>
            <div>{jobAlert.query.searchKeyword}</div>
          </div>
          <div className='column col-6'>
            {this.preferencesTags}
          </div>
          <div className='column col-2'>
            <Select
              {...this.mockedReduxFormProps}
              options={jobAlertsFrequencyOptions()}
              remoteChange
              onSelectBlur={this.onSelectBlur}
              name='emailFrequency'
              onSelect={this.onUpdateJobAlert}
              withIcon
            />
          </div>
          <div className='col-2'>
            <div className='job-alert-item__buttons'>
              <SmallJustIconButton
                onClick={this.openEditJobAlertForm}
              >
                <i className='ion-md-create' />
              </SmallJustIconButton>
              <SmallJustIconButton
                onClick={this.openDeleteModal}
              >
                <i className='ion-md-trash' />
              </SmallJustIconButton>
            </div>
          </div>
        </div>
        <Modal
          ariaHideApp={false}
          title={t('modals.editJobAlert.title')}
          show={this.state.isJobAlertFormOpen}
          handleSubmit={this.submitForm}
          handleClose={this.closeEditJobAlertForm}
          handleCancel={this.closeEditJobAlertForm}
          cancelText={t('common.cancel')}
          submitText={t('common.editJobAlert')}
          showContact={false}
          closeOnSubmit={false}
          showCancel
        >
          <p className='save-alerts-box__form-description'>{t('modals.editJobAlert.description')}</p>
          <EditJobAlertFormConnector jobAlert={jobAlert} />
        </Modal>
        <DeleteModal
          ariaHideApp={false}
          title={t('modals.deleteJobAlert.title')}
          content={t('modals.deleteJobAlert.description')}
          handleDelete={this.deleteJobAlert}
          isModalVisible={this.state.isDeleteJobAlertModalOpen}
          hideDeleteModal={this.hideDeleteModal}
        />
      </div>
    )
  }
}

JobAlertItem.propTypes = {
  jobAlert: PropTypes.object.isRequired,
  updateJobAlert: PropTypes.func.isRequired,
  canCloseModal: PropTypes.bool,
  submitForm: PropTypes.func.isRequired
}

export default withTranslations(JobAlertItem)



