import { connect } from 'react-redux'
import { isValid, submit } from 'redux-form'
import { updateJobAlert, deleteJobAlert } from 'Shared/ducks/jobAlerts'
import { JobAlertItem, JobAlertItemMobile } from 'Candidate/views/JobAlerts/components/JobAlertItem'
import { formName } from './EditJobAlertFormConnector'
import { isMobile } from 'Shared/helpers/mobile'

const JobAlertItemComponent = isMobile() ? JobAlertItemMobile : JobAlertItem

const mapStateToProps = state => ({
  canCloseModal: isValid(formName)(state)
})

const mapDispatchToProps = dispatch => ({
  updateJobAlert: payload => dispatch(updateJobAlert(payload)),
  deleteJobAlert: id => dispatch(deleteJobAlert(id)),
  submitForm: () => dispatch(submit(formName))
})

export default connect(mapStateToProps, mapDispatchToProps)(JobAlertItemComponent)



