import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { t } from 'i18next'
import notie from 'notie'
import withTranslations from 'Shared/hoc/withTranslations/index'
import { getLabels } from 'Shared/helpers/collection'
import { StaticTagMobile } from 'Shared/components/StaticTag/index'
import { SmallJustIconButton } from 'Shared/components/Button/index'
import { FormModalMobile, ModalMobile, PreviewModalMobile } from 'Shared/components/Modal'
import EditJobAlertFormConnector from 'Candidate/connectors/Forms/JobAlerts/EditJobAlertFormConnector'

const R = require('ramda')

const MAX_LABELS_COUNT = 2

class JobAlertItemMobile extends Component {
  state = {
    isJobAlertFormOpen: false,
    isDeleteJobAlertModalOpen: false,
    isPreferencesPreviewOpen: false
  }

  get labelsForPreferences () {
    const filterPreferences = R.pipe(
      R.omit(['searchKeyword', 'emailFrequency']),
      getLabels
    )(this.props.jobAlert.query)

    return [...filterPreferences, this.props.jobAlert.query.searchKeyword]
  }

  get seeAllLabelsButton () {
    return <StaticTagMobile
      onClick={this.openPreferencesPreview}
      key='see-all'
      name={`+${this.labelsForPreferences.length - MAX_LABELS_COUNT}`}
    />
  }

  get preferencesTags () {
    const labels = this.labelsForPreferences
    const items = labels.slice(0, MAX_LABELS_COUNT).map((label, index) => <StaticTagMobile
      key={`${index}-${label}`}
      name={label}
    />)

    labels.length > MAX_LABELS_COUNT && items.push(
      <div>
        {this.seeAllLabelsButton}
        <PreviewModalMobile
          title='preferences'
          show={this.state.isPreferencesPreviewOpen}
          handleClose={this.closePreferencesPreview}
        >
          { labels.map((label, index) =>
            <StaticTagMobile
              large
              key={`${index}-${label}`}
              name={label}
            />)
          }
        </PreviewModalMobile>
      </div>
    )

    return items
  }

  openPreferencesPreview = () => this.setState({ isPreferencesPreviewOpen: true })
  closePreferencesPreview = () => this.setState({ isPreferencesPreviewOpen: false })

  openEditJobAlertForm = () => this.setState({ isJobAlertFormOpen: true })
  closeEditJobAlertForm = () => this.setState({ isJobAlertFormOpen: false })

  openDeleteModal = () => this.setState({ isDeleteJobAlertModalOpen: true })
  hideDeleteModal = () => this.setState({ isDeleteJobAlertModalOpen: false })

  deleteJobAlert = async () => {
    try {
      await this.props.deleteJobAlert(this.props.jobAlert.id)
      this.hideDeleteModal()
      notie.alert({
        type: 'success',
        text: t('alerts.deleteJobAlertSuccess')
      })
    } catch (e) {
      notie.alert({
        type: 'error',
        text: t('alerts.deleteJobAlertError')
      })
    }
  }

  render () {
    const { jobAlert, submitForm, canCloseModal } = this.props

    return (
      <div className='job-alert-item-mobile__container'>
        <div className='columns'>
          <div className='column col-8'>
            <div className='job-alert-item-mobile__title'>{jobAlert.query.searchKeyword}</div>
            <div className='job-alert-item-mobile__description'>{jobAlert.frequency}</div>
            <div className='job-alert-item-mobile__tags'>{this.preferencesTags}</div>
          </div>
          <div className='column col-4'>
            <div className='job-alert-item-mobile__buttons'>
              <SmallJustIconButton
                onClick={this.openEditJobAlertForm}
              >
                <i className='ion-md-create' />
              </SmallJustIconButton>
              <SmallJustIconButton
                onClick={this.openDeleteModal}
              >
                <i className='ion-md-trash' />
              </SmallJustIconButton>
            </div>
          </div>
        </div>
        <FormModalMobile
          title={t('modals.editJobAlert.title')}
          show={this.state.isJobAlertFormOpen}
          handleClose={this.closeEditJobAlertForm}
          handleSubmit={submitForm}
          submitText={t('common.editJobAlert')}
          cancelText={t('common.cancel')}
          disableSubmit={!canCloseModal}
        >
          <p className='job-alerts-mobile__form-description'>{t('modals.createJobAlert.description')}</p>
          <EditJobAlertFormConnector jobAlert={jobAlert} />
        </FormModalMobile>
        <ModalMobile
          title={t('modals.deleteJobAlert.title')}
          show={this.state.isDeleteJobAlertModalOpen}
          handleClose={this.hideDeleteModal}
          handleSubmit={this.deleteJobAlert}
          submitText={t('common.delete')}
          cancelText={t('common.cancel')}
        >
          {t('modals.deleteJobAlert.description')}
        </ModalMobile>
      </div>
    )
  }
}

JobAlertItemMobile.propTypes = {
  jobAlert: PropTypes.object.isRequired,
  updateJobAlert: PropTypes.func.isRequired,
  canCloseModal: PropTypes.bool,
  submitForm: PropTypes.func.isRequired
}

export default withTranslations(JobAlertItemMobile)



