import React, { Component } from 'react'
import AlarmIcon from 'Shared/assets/images/vectors/alarm.svg'
import withTranslations from 'Shared/hoc/withTranslations'

class JobAlertsEmptyState extends Component {
  render () {
    const { t } = this.props

    return (
      <div>
        <div className='job-alerts__empty'>
          <div className='job-alerts__empty-icon-container'>
            <img className='job-alerts__empty-icon' src={AlarmIcon} />
          </div>
          <div className='job-alerts__empty-description'>
            {t('candidate.jobAlerts.emptyState')}
          </div>
        </div>
      </div>
    )
  }
}

export default withTranslations(JobAlertsEmptyState)



