import { createSelector } from 'reselect'
import { transformToArray } from 'Shared/helpers/redux'

const getJobAlerts = state => state.jobAlerts.entries

export const getJobAlertsAsArray = createSelector(
  getJobAlerts,
  jobAlerts => transformToArray(jobAlerts)
)



