import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import JobAlertItemConnector from 'Candidate/connectors/Forms/JobAlerts/JobAlertItemConnector'
import withTranslations from 'Shared/hoc/withTranslations/index'

class JobAlertsListMobile extends PureComponent {
  render () {
    return (
      <div className='job-alerts-mobile-list__container'>
        <div className='job-alerts-mobile-list__list-container'>
          {this.props.jobAlerts.map(jobAlert => <JobAlertItemConnector key={jobAlert.id} jobAlert={jobAlert} />)}
        </div>
      </div>
    )
  }
}

JobAlertsListMobile.propTypes = {
  jobAlerts: PropTypes.array.isRequired
}

export default withTranslations(JobAlertsListMobile)



