import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import JobAlertItemConnector from 'Candidate/connectors/Forms/JobAlerts/JobAlertItemConnector'
import withTranslations from 'Shared/hoc/withTranslations/index'

class JobAlertsList extends PureComponent {
  render () {
    const { t } = this.props

    return (
      <div>
        <div className='job-alerts-list__header'>
          <div className='columns'>
            <div className='column col-2'>
              <div className='job-alert-item__title'>{t('common.keyword')}</div>
            </div>
            <div className='column col-6'>
              <div className='job-alert-item__title'>{t('common.preferences')}</div>
            </div>
            <div className='column col-2'>
              <div className='job-alert-item__title'>{t('common.emailFrequency')}</div>
            </div>
          </div>
        </div>
        {this.props.jobAlerts.map(jobAlert => <JobAlertItemConnector key={jobAlert.id} jobAlert={jobAlert} />)}
      </div>
    )
  }
}

JobAlertsList.propTypes = {
  jobAlerts: PropTypes.array.isRequired
}

export default withTranslations(JobAlertsList)



