import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import JobsViewBox from 'Candidate/components/JobsViewBox'
import withTranslations from 'Shared/hoc/withTranslations/index'
import Container from 'Shared/components/Container/index'
import { JobAlertsList } from './components/JobAlertsList'
import { JobAlertsEmptyState } from './components/JobAlertsEmptyState'
import JobOfferListSignUp from 'Candidate/views/JobsView/components/JobOfferListSignUp/JobOfferListSignUp'
import cx from 'classnames'

import './JobAlerts.scss'
import { Roles } from 'Shared/constants/User'
import { Helmet } from 'react-helmet'

const R = require('ramda')

class JobAlerts extends PureComponent {
  componentDidMount () {
    (this.props.userRole === Roles.candidate.name) && this.props.fetchJobAlerts(this.props.candidateId)
  }

  get jobAlerts () {
    return R.ifElse(
      R.isEmpty,
      R.always(<JobAlertsEmptyState />),
      R.always(<JobAlertsList jobAlerts={this.props.jobAlerts} />)
    )(this.props.jobAlerts)
  }

  render () {
    const { t, userRole } = this.props
    const boxClass = cx({
      'job-alerts': userRole === Roles.guest.name
    })

    return (
      <div>
        <Helmet>
          <title>{t('meta.candidate.title.jobAlerts')}</title>
          <meta property='og:title' content={t('meta.candidate.title.jobAlerts')} />
        </Helmet>
        <JobsViewBox className={boxClass} userRole={userRole}>
          <Container>
            {
              userRole !== Roles.guest.name && (
                <div className='job-alerts__container'>
                  <div className='job-alerts__heading'>
                    <h2 className='job-alerts__title'>{t('candidate.jobAlerts.title')}</h2>
                    <div className='job-alerts__description'>{t('candidate.jobAlerts.description')}</div>
                  </div>
                  <div className='job-alerts__list'>
                    {this.jobAlerts}
                  </div>
                </div>
              )
            }
          </Container>
        </JobsViewBox>
        <Container>
          {
            userRole === Roles.guest.name && (
              <JobOfferListSignUp
                title={t('candidate.jobAlerts.guestTitle')}
                description={t('candidate.jobAlerts.guestDescription')}
              />
            )
          }
        </Container>
      </div>
    )
  }
}

JobAlerts.propTypes = {
  fetchJobAlerts: PropTypes.func.isRequired,
  jobAlerts: PropTypes.array.isRequired,
  candidateId: PropTypes.string.isRequired
}

export default compose(withRouter, withTranslations)(JobAlerts)



