import { createSelector } from 'reselect'
import { transformToArray } from 'Shared/helpers/redux'

const getJobOffersBookmarks = state => state.jobOffersBookmarks.entries

export const getJobOffersBookmarksAsArray = createSelector(
  getJobOffersBookmarks,
  jobOffersBookmarks => transformToArray(jobOffersBookmarks)
)



