import React, { PureComponent, Fragment } from 'react'
import PropTypes from 'prop-types'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import JobsViewBox from 'Candidate/components/JobsViewBox'
import withTranslations from 'Shared/hoc/withTranslations/index'
import Container from 'Shared/components/Container/index'
import JobOfferCardAlgoliaConnector from 'Candidate/connectors/JobOfferCardAlgolia/JobOfferCardAlgoliaConnector'
import DocsIcon from 'Shared/assets/images/vectors/docs.svg'

import './SavedJobs.scss'
import { Roles } from 'Shared/constants/User'
import JobOfferListSignUp from 'Candidate/views/JobsView/components/JobOfferListSignUp/JobOfferListSignUp'
import { Helmet } from 'react-helmet'

class SavedJobs extends PureComponent {
  componentDidMount () {
    (this.props.userRole === Roles.candidate.name) && this.props.fetchJobOffersBookmarksForCandidate(this.props.candidateId)
  }

  get jobs () {
    return this.props.jobOffersBookmarks.map(job => (
      <JobOfferCardAlgoliaConnector
        key={job.id}
        hit={job}
      />
    ))
  }

  get emptyState () {
    const { t } = this.props
    return <div className='saved-jobs__empty'>
      <div className='saved-jobs__empty-icon-container'>
        <img className='saved-jobs__empty-icon' src={DocsIcon} />
      </div>
      <div className='saved-jobs__empty-description'>
        {t('candidate.savedJobs.emptyState')}
      </div>
    </div>
  }

  getJobsContent = () => {
    const { t, userRole, jobOffersBookmarks } = this.props

    switch (true) {
      case userRole === Roles.guest.name:
        return (
          <JobOfferListSignUp
            title={t('candidate.savedJobs.guestTitle')}
            description={t('candidate.savedJobs.guestDescription')}
          />
        )
      case jobOffersBookmarks.length > 0:
        return (
          <Fragment>
            <h3 className='saved-jobs__title'>{t('candidate.savedJobs.title')}</h3>
            <div className='job-offers-view__container'>
              {this.jobs}
            </div>
          </Fragment>
        )
      default:
        return this.emptyState
    }
  }

  render () {
    const { t } = this.props

    return (
      <div>
        <Helmet>
          <title>{t('meta.candidate.title.savedJobs')}</title>
          <meta property='og:title' content={t('meta.candidate.title.savedJobs')} />
        </Helmet>
        <JobsViewBox className='saved-jobs' />
        <Container>
          <div className='saved-jobs__content'>
            {this.getJobsContent()}
          </div>
        </Container>
      </div>
    )
  }
}

SavedJobs.propTypes = {
  fetchJobOffersBookmarksForCandidate: PropTypes.func.isRequired,
  candidateId: PropTypes.string.isRequired,
  jobOffersBookmarks: PropTypes.array.isRequired
}

export default compose(withRouter, withTranslations)(SavedJobs)



