import { connect } from 'react-redux'
import { SavedJobs, SavedJobsMobile } from 'Candidate/views/SavedJobs'
import { fetchJobOffersBookmarksForCandidate } from 'Shared/ducks/jobOffersBookmarks'
import { getJobOffersBookmarksAsArray } from 'Shared/selectors/jobOffersBookmarks'
import { isMobile } from 'Shared/helpers/mobile'
import { getUserRole } from 'Shared/selectors/user'

const SavedJobsComponent = isMobile() ? SavedJobsMobile : SavedJobs

const mapStateToProps = state => ({
  jobOffersBookmarks: getJobOffersBookmarksAsArray(state),
  userRole: getUserRole(state),
  candidateId: state.user.details.id
})

const mapDispatchToProps = {
  fetchJobOffersBookmarksForCandidate
}

export default connect(mapStateToProps, mapDispatchToProps)(SavedJobsComponent)



