import React, { Component } from 'react'
import AlarmIcon from 'Shared/assets/images/vectors/alarm.svg'
import { PrimaryButtonOutline } from 'Shared/components/Button'
import withTranslations from 'Shared/hoc/withTranslations'
import { FormModalMobile } from 'Shared/components/Modal'
import CreateJobAlertFormConnector from 'Candidate/connectors/Forms/JobAlerts/CreateJobAlertFormConnector'

class JobAlertsEmptyStateMobile extends Component {
  state = {
    isModalOpen: false
  }

  openModal = () => this.setState({ isModalOpen: true })
  closeModal = () => this.setState({ isModalOpen: false })

  render () {
    const { t, submitForm, isFormValid } = this.props

    return (
      <div>
        <div className='job-alerts-mobile__empty'>
          <div className='job-alerts-mobile__empty-icon-container'>
            <img className='job-alerts-mobile__empty-icon' src={AlarmIcon} />
          </div>
          <div className='job-alerts-mobile__empty-description'>
            {t('candidate.jobAlerts.emptyState')}
          </div>
          <div className='job-alerts-mobile__empty-cta-btn'>
            <PrimaryButtonOutline onClick={this.openModal}>
              {t('common.createJobAlert')}
            </PrimaryButtonOutline>
          </div>
        </div>
        <FormModalMobile
          title={t('common.createJobAlerts')}
          show={this.state.isModalOpen}
          handleClose={this.closeModal}
          handleSubmit={submitForm}
          submitText={t('common.createJobAlert')}
          cancelText={t('common.cancel')}
          disableSubmit={!isFormValid}
        >
          <p className='job-alerts-mobile__form-description'>{t('modals.createJobAlert.description')}</p>
          <CreateJobAlertFormConnector />
        </FormModalMobile>
      </div>
    )
  }
}

export default withTranslations(JobAlertsEmptyStateMobile)



